import React,{useState} from 'react'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Button} from 'react-bootstrap'
import CheckIcon from '@material-ui/icons/Check';
import axios from 'axios';
import {base_url} from '../../contexts/DataContext'

export default function FormF({value,submitForm,setStep}) {
       
    const [error,setError] = useState('')

     const onSubmit =async (e)=>{
        
        if(document.getElementById('image-input').files.length<2 ) {
            setError('u need to provide at least two images')
            return
        }
        //await axios.post('http://localhost:5000/add-property-images',e)
      //  e.preventDefault()
        submitForm()
    }

    function onPrev(){
        setStep(5)
    }
    return (
    
        <form method="POST" 
            encType="multipart/form-data" 
            className="text-center"
            onSubmit={onSubmit}
            action = "https://e-property-bid-backend.herokuapp.com/add-property-images"
            >

            <h5 id="simple-modal-title" >
                <b>Upload Images</b>
            </h5>
            <div className="text-danger">{error?`* ${error}`:''}</div>
            <br/>
            <div className="mb-3" style={{height:"150px",paddingTop:"30px"}}>
            <input 
            id="image-input"
            required 
            multiple 
            accept="image/jpeg, image/png, image/jpg , image/webp"
            placeholder="upload some more photos" 
            type="file" 
            name="myphotos" 
            className="htmlForm-control" />
            
            </div>
            <div>
                <Button variant="primary" onClick={onPrev} style={{marginRight:"15px"}}>
                    <ArrowBackIcon/>
                    <span style={{marginLeft:"15px"}}>Prev</span>
                    
                </Button>
               
                <Button variant="success" type="submit" style={{marginLeft:"15px"}}>
                    <CheckIcon/>
                    <span style={{marginLeft:"15px"}}>Done</span>
                </Button>
                
            </div>
        </form>
    )
}
