import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import {Button} from '@material-ui/core';
import {useData,ACTIONS} from '../contexts/DataContext'


const useStyles = makeStyles((theme) => ({
  root: {
    margin:'0 auto',
    width:'90%',
    height:'200px',
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
  },
}));

export default function Search() {
  const classes = useStyles();
  const {state,dispatch} = useData()

  const [propertyType,setPropertyType] = useState('')
  const  [purpose,setPurpose] = useState('')
  const [rentFrequency,setRentFrequency] = useState('')
  const [minPrice,setMinPrice]= useState('')
  const [maxPrice,setMaxPrice] = useState('')
  const [rooms,setRooms] = useState('')
  const [bathRooms,setBathRooms] = useState('')
  const [maxArea,setMaxArea] = useState('')

  function handleSubmit(e){
    e.preventDefault()
    if(propertyType==''&& purpose=='' && bathRooms=='' && maxArea=='' &&
    rentFrequency == '' && minPrice=='' && maxPrice=='' && rooms=='' ){
    dispatch({type:ACTIONS.RANDOM_DATA})
    return 
    }
    dispatch({type:ACTIONS.FILTER_DATA,
      payload:{propertyType,purpose,rentFrequency,minPrice,maxArea,bathRooms,maxPrice,rooms}})
  }
  return (
    <form onSubmit={handleSubmit}>
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={3}>
                <FormControl className={classes.formControl}>
                <InputLabel id="label1">Purpose</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="select1"
                  value={purpose}
                  onChange={(e)=>{setPurpose(e.target.value)}}
                >
                  <MenuItem value=""><em>None</em></MenuItem>
                  <MenuItem value={'for sale'}>To Buy</MenuItem>
                  <MenuItem value={'for rent'}>To Rent</MenuItem>
                </Select>
              </FormControl>
        </Grid>
        <Grid item xs={3}>
              <FormControl className={classes.formControl}>
                <InputLabel id="label2" >Rent Frequency</InputLabel>
                <Select
                  labelId="label2"
                  id="select2"
                  value={rentFrequency}
                  onChange={(e)=>{setRentFrequency(e.target.value)}}
                >
                  <MenuItem value=""><em>None</em></MenuItem>
                  <MenuItem value="monthly">Monthly </MenuItem>
                  <MenuItem value='yearly'>Yearly</MenuItem>
                </Select>
              </FormControl>
        </Grid>
        <Grid item xs={3}>
                <FormControl className={classes.formControl}>
                    <InputLabel id="label3">Property Type</InputLabel>
                    <Select
                      labelId="label3"
                      id="select3"
                      value={propertyType}
                      onChange={(e)=>{setPropertyType(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value="Residential">Residential </MenuItem>
                      <MenuItem value="Apartmental">Apartmental</MenuItem>
                      <MenuItem value="Commercial">Commercial</MenuItem>
                      <MenuItem value="Industrial">Industrial</MenuItem>
                      <MenuItem value="Agricultural">Agricultural</MenuItem>
  
                    </Select>
                  </FormControl>
        </Grid>
        <Grid item xs={3}>
              <FormControl className={classes.formControl}>
                    <InputLabel id="label4">Max price</InputLabel>
                    <Select
                      labelId="label4"
                      id="select4"
                      value={maxPrice}
                      onChange={(e)=>{setMaxPrice(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value={3000000}>30,00,000 </MenuItem>
                      <MenuItem value={4000000}>40,00,000</MenuItem>
                      <MenuItem value={500000}>50,00,000</MenuItem>
                      <MenuItem value={8500000}>85,00,000</MenuItem>
                      <MenuItem value={9010000}>90,10,000</MenuItem>
                      <MenuItem value={9530000}>95,30,000</MenuItem>
                      <MenuItem value={10050500}>1,00,50,500</MenuItem>
                    </Select>
                </FormControl>
        </Grid>
        <Grid item xs={3}>
                <FormControl className={classes.formControl}>
                    <InputLabel id="label5">Min price</InputLabel>
                    <Select
                      labelId="label5"
                      id="select5"
                      value={minPrice}
                      onChange={(e)=>{setMinPrice(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value={150000}>1.5 Lakh </MenuItem>
                      <MenuItem value={200000}>2 Lakh</MenuItem>
                      <MenuItem value={500000}>5 Lakh</MenuItem>
                      <MenuItem value={700000}>7 Lakh</MenuItem>
                      <MenuItem value={1000000}>10 Lakh</MenuItem>
                      <MenuItem value={1500000}>15 Lakh</MenuItem>
                      <MenuItem value={2000000}>20 Lakh</MenuItem>
                    </Select>
                </FormControl>
        </Grid>
        <Grid item xs={3}>
                <FormControl className={classes.formControl}>
                    <InputLabel id="label6">Number of Bathrooms</InputLabel>
                    <Select
                      labelId="label6"
                      id="select6"
                      value={bathRooms}
                      onChange={(e)=>{setBathRooms(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value={1}>1</MenuItem>
                      <MenuItem value={2}>2</MenuItem>
                      <MenuItem value={4}>4 </MenuItem>
                      <MenuItem value={6}>6 </MenuItem>
                      <MenuItem value={8}>8 </MenuItem>
                      <MenuItem value={10}>10</MenuItem>

                    </Select>
                </FormControl>
        </Grid>
        <Grid item xs={3}>
        <FormControl className={classes.formControl}>
                    <InputLabel id="label7">Max Area (sqft)</InputLabel>
                    <Select
                      labelId="label7"
                      id="select7"
                      value={maxArea}
                      onChange={(e)=>{setMaxArea(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value={300}>300 </MenuItem>
                      <MenuItem value={500}>500</MenuItem>
                      <MenuItem value={700}>700</MenuItem>
                      <MenuItem value={900}>900</MenuItem>
                      <MenuItem value={1000}>1000</MenuItem>
                    </Select>
                </FormControl>
        </Grid>
        <Grid item xs={3} style={{backgroundColor:""}}>
                <FormControl className={classes.formControl}>
                    <InputLabel id="label8">Rooms</InputLabel>
                    <Select
                      labelId="label8"
                      id="select8"
                      value={rooms}
                      onChange={(e)=>{setRooms(e.target.value)}}
                    >
                      <MenuItem value=''><em>None</em></MenuItem>
                      <MenuItem value={2}>2 </MenuItem>
                      <MenuItem value={4}>4</MenuItem>
                      <MenuItem value={6}>6</MenuItem>
                      <MenuItem value={8}>8</MenuItem>
                      <MenuItem value={10}>10</MenuItem>
                      <MenuItem value={12}>12</MenuItem>
                      <MenuItem value={14}>14</MenuItem>
                      <MenuItem value={16}>16</MenuItem>
                      <MenuItem value={18}>18</MenuItem>
                      <MenuItem value={20}>20 more</MenuItem>
                    </Select>
                </FormControl>
        </Grid>
        
      </Grid>
      <Button style={{textAlign:'center'}} variant="contained" color="primary" type="submit">
            Filter
          </Button>
    </div>
    </form>
  );
}

    



