import React,{useRef,useState} from 'react'
import { Card, Form,Button,Alert ,Container} from 'react-bootstrap'
import {useAuth} from '../contexts/AuthContext'
import {Link,useHistory} from 'react-router-dom'


export default function UpdateProfile() {
    const emailRef = useRef()
    const passRef = useRef()
    const confirmPassRef = useRef()
    const {currentUser,updateemail,updatepassword} = useAuth()
    const [error,setError]  = useState()
    const [loading,setLoading] = useState(false)
    const [message,setMessage] = useState('')
    const history = useHistory()
   
    
    async function handleSubmit(e){
        e.preventDefault()

        if(passRef.current.value !== confirmPassRef.current.value)
            return setError('pass do not match') 
        
        const promises = []
        setLoading(true)
        setError("")
        if(emailRef.current.value !== currentUser.email){
            promises.push(updateemail(emailRef.current.value))
        }

        if(passRef.current.value){
            promises.push(updatepassword(passRef.current.value))
        }

        Promise.all(promises).then(()=>{
            history.push('/')
        }).catch(err=>{
            setError(`failed to update account`)
        }).finally(()=>{
            setLoading(false)
        })
        // try{
        //     setError('')
        //     setLoading(true)
        //     await updateemail(emailRef.current.value)
        //      await updatepassword(passRef.current.value)
        //     history.push('/')
        // }catch(err){
        //     console.log(err)
        //     setError(`failed to create account`)
        // }
        // setLoading(false)
    }
    


    return (
        <>
        <Container className="d-flex justify-content-center 
            align-items-center"
            style={{minHeight:"100vh"}}>
            <div className="w-100" style={{maxWidth:'400px'}}>
        <Card>
            <Card.Body>
                <h2 className="text-center mb-4">Update Profile</h2>
                {message && <Alert variant="success">{message}</Alert>}
                {error && <Alert variant="danger">{error}</Alert>}
                <Form onSubmit={handleSubmit}>
                    <Form.Group id="email">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email" ref={emailRef}  
                        defaultValue={currentUser.email}/>
                    </Form.Group>
                    <Form.Group id="password">
                        <Form.Label>new password</Form.Label>
                        <Form.Control type="password" ref={passRef} 
                        placeholder="Leave blank to keep the same"/>
                    </Form.Group>
                    <Form.Group id="confirm-password">
                        <Form.Label>Confirm new password</Form.Label>
                        <Form.Control type="password" ref={confirmPassRef} 
                        placeholder="Leave blank to keep the same"/>
                    </Form.Group>
                    <Button type="submit" disabled={loading} className="w-100">Update Credentials</Button>
                </Form>
            </Card.Body>
        </Card>
        <div className="w-100 text-center mt-2">
         <Link to="/">cancel</Link>
        </div>
        </div>
    </Container>
        </>
    )
}
