import React,{useEffect,useState} from 'react'
import Carousal from './Carousal'
import {useParams} from 'react-router-dom'
import Maps from './Map'
import axios from 'axios'
import PropertyDetail from './PropertyDetail'
import PropertyByCities from './PropertyByCities'
import Nav from './Nav'
import Footer from './Footer'
import {base_url} from '../contexts/DataContext'

export default function PropertyInfo() {
    const {propertyId} = useParams()
    
    const [imgArr,setImgArr] = useState([])
    const [propertyData,setPropertyData]= useState({})
    useEffect(()=>{
       axios(`${base_url}/property/${propertyId}`)
        .then(data=>{
            
            setImgArr([data.data[0].coverPhoto[0],...data.data[0].photos])
            setPropertyData(data.data[0])
        })
        
    },[propertyId])


    
    return (
            <div>
                <Nav/>
            <Carousal width='60%' imgArr={imgArr}/>
            
            <PropertyDetail data={propertyData} />
            <div className="" style={{width:"100%",height:"600px",position:'relative'}}>
            <Maps lat={propertyData?.geography?.lat?.$numberDecimal} lon={propertyData?.geography?.lon?.$numberDecimal}/>
            </div>
            
            
            
            <PropertyByCities/>
            <Footer/>
            </div>
    )
}

