import React,{useState} from 'react'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Button} from 'react-bootstrap'

export default function FormD({setStep,values,setValues}) {
    
    const {lat,lon,city,landmarks } = values
    const [error,setError] = useState('')

    function onNext(){
        if(lat==''||lon==''||city==''||landmarks==''){
            setError('all fields must be required')
            return
        }

        setStep(5)
    }
    function onPrev(){
        setStep(3)
    }

    return (<div className="text-center ">
            <h5 id="simple-modal-title" >
                <b>Geographic Details</b>
            </h5>
        
            <div className="text-danger">{error?`* ${error}`:''}</div>
            <br/>
            <div className="mx-auto" style={{width:"80%"}}>

                <div className="htmlForm-floating mx-auto d-flex justify-content-between" style={{width:"300px"}}>
                    <select 
                    value={city}
                    onChange={(e)=>{setValues({...values,city:e.target.value})}}
                    style={{width:"200px",height:"30px"}}
                    name="city" 
                    className="htmlForm-select" 
                    aria-label="Floating label select example">
                        <option value="">None</option>
                        <option value="Mumbai">Mumbai</option>
                        <option value="New Delhi">New delhi</option>
                        <option value="Kolkata">Kolkata</option>
                        <option value="Chennai">Chennai</option>
                        <option value="Bangalore">Bangalore</option>
                        <option value="Hyderabad">Hyderabad</option>
                    </select>
                    <label 
                    style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}
                    htmlFor="city">
                        City
                    </label>
                </div>

                <br/>
                <div className="mx-auto d-flex justify-content-between" style={{width:"300px"}}>
                    
                    <input 
                    value={lat}
                    onChange={(e)=>{setValues({...values,lat:e.target.value})}}
                    style={{width:"200px",height:"30px"}}
                    required 
                    type="text"  
                    name="lon" className=""/>
                    <span 
                    style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}
                    className="">
                        latitude 
                    </span>
                </div>
                <br/>
                <div className="mx-auto d-flex justify-content-between" style={{width:"300px"}}>
                    
                    <input 
                    value={lon}
                    onChange={(e)=>{setValues({...values,lon:e.target.value})}}
                    style={{width:"200px",height:"30px"}}
                    required type="text"  name="lon" className=""/>
                    <span style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}>longitude</span>
                </div>
                <br/>
                <div className="mb-3 mx-auto d-flex justify-content-between" style={{width:"300px"}}>
                    
                    <input 
                    value={landmarks}
                    onChange={(e)=>{setValues({...values,landmarks:e.target.value})}}
                    style={{width:"200px",height:"30px"}}
                    required type="text" placeholder="separate by comma ," name="landmarks" className="htmlForm-control" />
                    <label 
                    style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}
                    htmlFor="landmarks" 
                    className="htmlForm-label">
                        Landmarks
                    </label>
                </div>
            </div>
            <div>
                <Button variant="primary" onClick={onPrev} style={{marginRight:"15px"}}>
                    <ArrowBackIcon/>
                    <span style={{marginLeft:"15px"}}>Prev</span>
                    
                </Button>
                <Button variant="primary" onClick={onNext} style={{marginLeft:"15px"}}>
                    
                    <span style={{marginRight:"15px"}}>Next</span>
                    <ArrowForwardIcon/>
                </Button>
                
            </div>
        </div>
    )
}
