import React from 'react'

import {Carousel} from 'react-bootstrap'
import { base_url } from '../contexts/DataContext'

export default function Carousal({width,imgArr,details}) {

    // console.log(`i am imgArr`,imgArr)

    const styling={
        carouselItem :{
            maxHeight:'550px'
        },
        
        carouselImg: {
            objectFit:'cover',
            maxHeight:'450px',
        }
    }
    return (
        <div style={{width:`${width}`,margin:'25px auto'}}>
                <Carousel fade>

                {imgArr?.length >0 && imgArr.map(img=>{
                    return(
                    <Carousel.Item style={styling.carouselItem}>
                        <img
                        className="d-block w-100"
                        src={`${base_url}/image/${img}`}
                        alt="First slide"
                        style={styling.carouselImg}
                        />
                        <Carousel.Caption>
                        <h3>{details ? details.title:''}</h3>
                        <p>{details?details.desc:''}</p>
                        </Carousel.Caption>
                    </Carousel.Item> )
                })
                }
                
            </Carousel>
        </div>
    )
}
