
import DataProvider from './contexts/DataContext'
import Dashboard from './components/DashBoard'
import {HashRouter as Router ,Route,Switch} from 'react-router-dom'
import PropertyInfo from './components/PropertyInfo'
import Success from './components/Success'
import Cancel from './components/Cancel'
import PrivateRoute from './authentication/PrivateRoute'
import GlobalRoute from './authentication/GlobalRoute'
import Signin from './authentication/Signin'
import Signup from './authentication/Signup'
import UpdateProfile from'./authentication/UpdateProfile'
import Profile from './authentication/Profile'
import ForgotPassWord from './authentication/ForgotPassword'
import {AuthProvider} from './contexts/AuthContext'


function App() {
  return (
    <AuthProvider>
      <DataProvider>
        <Router>
            <Switch>
                <Route exact path="/" ><Dashboard/></Route>
                <PrivateRoute path="/property/:propertyId" component={PropertyInfo}></PrivateRoute>
                <PrivateRoute path="/success" component={Success}></PrivateRoute>
                <PrivateRoute path="/cancel" component={Cancel}></PrivateRoute>
                <PrivateRoute path="/profile" component={Profile}></PrivateRoute>
                <PrivateRoute path='/update-profile' component={UpdateProfile} ></PrivateRoute>
                <GlobalRoute path='/signin' component={Signin}></GlobalRoute>
                <GlobalRoute path='/signup' component={Signup}></GlobalRoute>
                <GlobalRoute path='/forgot-password' component={ForgotPassWord}></GlobalRoute>
            </Switch>
        </Router>
      </DataProvider>
    </AuthProvider>
    
  )
}

export default App
