import React,{useContext,useReducer,useEffect} from 'react'
import axios from 'axios'

const DataContext = React.createContext()
export let base_url = `https://e-property-bid-backend.herokuapp.com`

export const ACTIONS ={
    ADD_DATA:"addData",
    RANDOM_DATA:"randomData",
    FILTER_DATA:"filterData",
    FILTER_BY_CITY:"filterByCity"
}

export function useData(){
    return useContext(DataContext)
}
let allProperties = []

function reducer(state,{type,payload}){
    switch(type){
        case ACTIONS.ADD_DATA:
           allProperties = [...payload]
           let ptyArr = []
                    for(let i=0;i<20;){
                    let random = Math.floor(Math.random()*allProperties.length)
                    if(ptyArr.filter(ele=>ele._id == allProperties[random]._id).length != 0 ){
                            continue;
                        }
                        ptyArr.push(allProperties[random])
                        i++
                    }
                    
           return [...ptyArr]

        case ACTIONS.RANDOM_DATA:
            let arr = []

                    for(let i=0;i<20;){
                    let randomValue = Math.floor(Math.random()*allProperties.length)
                    if(arr.filter(ele=>ele._id == allProperties[randomValue]._id).length != 0 ){
                            continue;
                        }
                        arr.push(allProperties[randomValue])
                        i++}

            return [...arr]
        
        case ACTIONS.FILTER_DATA:
            let tempArr = []
            tempArr = [...allProperties]
            //console.log('i am payload',action.payload)
            
           // console.log('payload maxArea',payload.maxArea)
            tempArr=tempArr.filter(data=>data.propertyType ==(payload.propertyType=='' ? data.propertyType :payload.propertyType))
           // console.log('after property type',tempArr)
            tempArr=tempArr.filter(data=>data.rooms==(payload.rooms=='' ? data.rooms : payload.rooms))
           // console.log('after rooms',tempArr)
            tempArr=tempArr.filter(data=>data.purpose ==(payload.purpose == '' ? data.purpose : payload.purpose))
           // console.log('after purpose',tempArr)
            tempArr=tempArr.filter(data=>data.bath==(payload.bathRooms =='' ? data.bath : payload.bathRooms ))
           // console.log('after bathrooms',tempArr)
            tempArr=tempArr.filter(data=>Math.floor(data.area) <=(payload.maxArea==''? Math.floor(data.area): payload.maxArea))
           // console.log('after max area',tempArr)
            tempArr=tempArr.filter(data=>data.rentFrequency == (payload.rentFrequency=='' ? data.rentFrequency:payload.rentFrequency))
           // console.log('after rent frequency',tempArr)
            tempArr=tempArr.filter(data=>data.price >= (payload.minPrice == '' ? data.price : payload.minPrice ))
            //console.log('after min price',tempArr)
            tempArr=tempArr.filter(data=>data.price <= (payload.maxPrice ==''? data.price:payload.maxPrice ))
           // console.log('after max price',tempArr)

        return [...tempArr]

        case ACTIONS.FILTER_BY_CITY:
           let newArr = allProperties.filter((data)=>data.city===payload.city)
        return [...newArr]
        default :
            return state
    }
}

export default function DataProvider({children}) {

    const [state,dispatch] = useReducer(reducer,[])

    useEffect(()=>{

        async function handleData(){
          let data = await axios.get(`${base_url}/getProperty`)
          dispatch({type:ACTIONS.ADD_DATA,payload:data.data})
        } 

        handleData()
      },[])

    const value={
        dispatch,
        state
    }
    return (
        <DataContext.Provider value={value}>
            {children}
        </DataContext.Provider>
    )
}
