import React,{useState,useEffect} from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import {Button as RButton} from 'react-bootstrap'
import {Link} from 'react-router-dom' 
import {useData,ACTIONS} from '../contexts/DataContext'

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function SwipeableTemporaryDrawer({drawer,setdrawer}) {
  const classes = useStyles();
  const {dispatch} = useData()

  const [city,setCity] = useState('')

    

    

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setdrawer({ [anchor]: open });
  };

  useEffect(()=>{
        
    function handleClick(){
        if(city !== '') {
         dispatch({type:ACTIONS.FILTER_BY_CITY,payload:{city}})
        }
     }
     handleClick()
},[city])


  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      
      <div>
        <ListItem><Link to="/signin"><RButton variant="primary">Sign in</RButton></Link></ListItem>
        <Divider/>
        <ListItem><Link to="/signin"><RButton variant="primary">Post Properties</RButton></Link></ListItem>
        
        <ListItem><Link to="/signin"><RButton variant="primary">Pay Rent</RButton></Link></ListItem>
        <Divider/>
        <div >
        <ListItem ><p>Explore Properties By Cities</p></ListItem>
        <ListItem style={{cursor:'pointer'}} onClick={()=>{setCity('Mumbai')}}><p>Mumbai</p></ListItem>
        <ListItem style={{cursor:'pointer'}} onClick={()=>{setCity('New Delhi')}}><p>New Delhi</p></ListItem>
        <ListItem style={{cursor:'pointer'}} onClick={()=>{setCity('Kolkata')}}><p>Kolkata</p></ListItem>
        <ListItem style={{cursor:'pointer'}} onClick={()=>{setCity('Chennai')}}><p>Chennai</p></ListItem>
        <ListItem style={{cursor:'pointer'}} onClick={()=>{setCity('Hyderabad')}}><p>Hyderabad</p></ListItem>
        </div>
      </div>
      <Divider />
      {/* <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List> */}
    </div>
  );

  return (
    <div>
      {['right'].map((anchor) => (
        <React.Fragment key={anchor}>
          <SwipeableDrawer
            anchor={anchor}
            open={drawer[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
      
    </div>
  );
}
