import React,{useState} from 'react'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Button} from 'react-bootstrap'

export default function FormB({values,step,setValues,setStep}) {
    // const {rooms,floors,bath,area} = values


    const {rooms,bath,area,floors} = values
    const [error,setError] = useState('')
    

    const onNext =()=>{
        if(rooms==''||bath==''||area==''||floors==''){
            setError('all fields must be required')

            return
        }

        
        setStep(3)
    }

    function onPrev(){
        setStep(1)
    }
    return (
        <div className="text-center " >
            <h5 id="simple-modal-title" >
                <b>Tell us more about your Property</b>
            </h5>
            
            <div className="text-danger">{error?`* ${error}`:''}</div>
            <br/>
        <div className=" mx-auto" style={{width:"60%"}}>

            <div className="mb-3 d-flex justify-content-between" >
                <label htmlFor="rooms" className="htmlForm-label">Number of rooms</label>
                <input  
                value={rooms}
                onChange={(e)=> setValues({...values,rooms:e.target.value})}
                 required type="Number" name="rooms" 
                 className="htmlForm-control" />
            </div>
            <div className="mb-3 d-flex justify-content-between">
                <label htmlFor="floors" className="htmlForm-label" >Number of floors</label>
                <input value={floors}
                style={{float:'right'}}
                 onChange={(e)=> setValues({...values,floors:e.target.value})} required type="Number" name="floors" className="htmlForm-control" />
            </div>
            <div className="mb-3 d-flex justify-content-between">
                <label htmlFor="baths" className="htmlForm-label" >Number of bathrooms</label>
                <input value={bath}
                onChange={(e)=> setValues({...values,bath:e.target.value})} required type="Number"  name="baths" className="htmlForm-control" />
            </div>
            <div className="mb-3 d-flex justify-content-between">
                <label htmlFor="area" className="htmlForm-label" >Area of property</label>
                <input value={area}
                onChange={(e)=> setValues({...values,area:e.target.value})} required type="text"  name="area"  />
            </div>

        </div>
            <div>
                <Button variant="primary" onClick={onPrev} style={{marginRight:"15px"}}>
                    <ArrowBackIcon/>
                    <span style={{marginLeft:"15px"}}>Prev</span>
                    
                </Button>
                <Button variant="primary" onClick={onNext} style={{marginLeft:"15px"}}>
                    
                    <span style={{marginRight:"15px"}}>Next</span>
                    <ArrowForwardIcon/>
                </Button>
                
                
            </div>
        </div>
    )
}
