import React,{useRef,useState} from 'react'
import {Card,Form,Button,Alert,Container} from 'react-bootstrap'
import {useAuth} from '../contexts/AuthContext'
import {Link} from 'react-router-dom'

export default function ForgotPassword() {
    const emailRef = useRef()
    const {resetpassword} = useAuth()
    const [error,setError]  = useState()
    const [loading,setLoading] = useState(false)
    const [message,setMessage] = useState('')

    async function handleSubmit(e){
        e.preventDefault()

        try{
            setError('')
            setLoading(true)
            setMessage('')
            await resetpassword(emailRef.current.value)
            setMessage('check your inbox for further instructions')
        }catch(err){
            console.log(err)
            return setError(`failed to reset password`)
        }
        setLoading(false)
    }
    


    return (
        <>
        <Container className="d-flex justify-content-center 
      align-items-center"
        style={{minHeight:"100vh"}}>
            <div className="w-100" style={{maxWidth:'400px'}}>
                <Card>
                    <Card.Body>
                        <h2 className="text-center mb-4">Forgot Password</h2>
                        {message.length>2 && <Alert variant="success">{message}</Alert>}
                        {error && <Alert variant="danger">{error}</Alert>}
                        <Form onSubmit={handleSubmit}>
                            <Form.Group id="email">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" ref={emailRef} required/>
                            </Form.Group>
                            
                            <Button type="submit" disabled={loading} className="w-100 mt-4">Reset Password</Button>
                        </Form>
                    </Card.Body>
                </Card>
                <div className="w-100 text-center mt-2">
                    Go back to <Link to="/signin">Sign In</Link> ?
                </div>
            </div>
        </Container>
        </>
    )
}
