import React,{useState} from 'react'
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Button} from 'react-bootstrap'

export default function FormA({values,setValues,step,setStep}) {

    const {ptyType,purpose,rentF,price} = values
    const [error,setError] = useState('')

    const onNext =()=>{
        if(ptyType==''||purpose==''||price==0){
            setError('all fields must be required')
            return
        }

        if(purpose=='for rent'&&rentF==''){
            setError('all fields must be required')
            return
        }
        

        
        setStep(2)
    }
    return (
        <div className="text-center">
            <h5 id="simple-modal-title" >
                <b>Basic Details</b>
            </h5>
            
            <div className="text-danger">{error?`* ${error}`:''}</div>
            <br/>
            <div className="mx-auto" style={{width:"80%"}}>

                <div className="htmlForm-floating">
                    <select onChange={(e)=> setValues({...values,ptyType:e.target.value})  }
                    value={ptyType}
                    name="propertyType" 
                    style={{width:"200px",height:"30px"}}
                    className="htmlForm-select" 
                    aria-label="Floating label select example">
                        <option value="">None</option>
                        <option value="Residential">Residential</option>
                        <option value="Apartmental">Apartmental</option>
                        <option value="Commercial">Commercial</option>
                        <option value="Industrial">Industrial</option>
                        <option value="Agricultural">Agricultural</option>
                    </select>
                    <label htmlFor="propertyType" 
                    style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}>
                        Property type
                    </label>
                </div>

                <br/>
                <div className="htmlForm-floating ">
                    <select value={purpose} 
                    onChange={(e)=> setValues({...values,purpose:e.target.value})  } 
                    name="purpose" 
                    style={{width:"200px",height:"30px"}}
                    className="htmlForm-select" 
                    aria-label="Floating label select example">
                        <option value="">None</option>
                        <option value="for sale">For Sale</option>
                        <option value="for rent">For Rent</option>
                    </select>
                    <label htmlFor="purpose" style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}>
                        <div className=""> 
                        Purpose
                        </div>
                        
                    </label>
                </div>

                <br/>
                <div value={rentF} className="htmlForm-floating" style={{display:`${purpose=='for rent'?'block':'none'}`}}>
                    <select 
                    style={{width:"200px",height:"30px",marginBottom:"30px"}}
                    onChange={(e)=> setValues({...values,rentF:e.target.value})  } 
                    name="rentFrequency" 
                    className="htmlForm-select" 
                    aria-label="Floating label select example">
                        <option value="">None</option>
                        <option value="monthly">Monthly</option>
                        <option value="yearly">Yearly</option>
                    </select>
                    <label htmlFor="rentFrequency" style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}>Rent Frequency</label>
                </div>

                <div className="mb-3">
                    
                    <input value={price} onChange={(e)=> setValues({...values,price:e.target.value})} required type="Number" name="price" className="htmlForm-control" />
                    <label 
                    htmlFor="price" 
                    style={{width:"30%",textAlign:'left',paddingLeft:"10px"}}
                    className="htmlForm-label">
                        {purpose=='for sale'?'Property Price':'Rent Price'}
                    </label>
                </div>
            </div>

            <div>
                
                <Button variant="primary" onClick={onNext} >
                    
                    <span style={{marginRight:"15px"}}>Next</span>
                    <ArrowForwardIcon/>
                </Button>
                
            </div>
        </div>
    )
}
