import React,{useState} from 'react'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Button} from 'react-bootstrap'

export default function FormE({setStep,values,setValues}) {

    const {title,desc} = values
    const [error,setError] = useState('')

    function onNext(){
        if(title==''||desc==''){
            setError('all fields must be required')
            return
        }

        setStep(6)
    }
    function onPrev(){
        setStep(4)
    }

    return (
        <div className="text-center">
            <h5 id="simple-modal-title" >
                <b>Give a short brief</b>
            </h5>
            
            
            <div className="text-danger">{error?`* ${error}`:''}</div>
            <br/>
            <div className="mb-3 mx-auto" style={{width:"80%"}}>
                <label 
                htmlFor="title" 
                className="htmlForm-label" 
                style={{marginRight:"20px"}}><b>Title :</b></label>
                <input 
                value={title}
                onChange={(e)=>{setValues({...values,title:e.target.value})}}
                style={{width:"80%"}}
                required 
                type="text"  
                name="title" 
                className="htmlForm-control" />
            </div>
            <div className="mb-3">
                <label htmlFor="description" className="htmlForm-label"><b>Description</b></label>
                <br/>
                <textarea 
                value={desc}
                onChange={(e)=>{setValues({...values,desc:e.target.value})}}
                 style={{width:"80%",minHeight:"100px"}}
                required name="description" className="htmlForm-control" id="examplehtmlFormControlTextarea1" rows="3"></textarea>
            </div>
            <div>
                <Button variant="primary" onClick={onPrev} style={{marginRight:"15px"}}>
                    <ArrowBackIcon/>
                    <span style={{marginLeft:"15px"}}>Prev</span>
                    
                </Button>
                <Button variant="primary" onClick={onNext} style={{marginLeft:"15px"}}>
                     
                    <span style={{marginRight:"15px"}} >Next</span>
                    <ArrowForwardIcon/>
                </Button>
                
            </div>
        </div>
    )
}
