import React,{useState} from 'react'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import {Button} from 'react-bootstrap'
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';


export default function FormC({setStep,values,setValues}) {

    const { mobile,govId,govIdType,address} = values
    const [error,setError] = useState()

    function verifyMobileNo(){
        setError('')
        if(mobile.length!==10){
        setError('Mobile Number is need to be of 10 digits')
            return 1
        }
        let str = mobile.toString()
        if(/[A-Z]]/.test(str)){
        setError('Mobile Number doesn"t contain alphabets ')
            return 1
        }
        if(/[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/.test(str)){
            setError("Mobile Number doesn't contain special characters ")
                return 1
            }
        return 0
    }

    function VerifyPanNo(str){
        setError('')
        if(str.length!==10){
            setError('PAN Id is need to be of 10 digits')
            return 1
        }
        //num= num.replace(/ /g,'')
        var format = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/;
        if(format.test(str)){
            setError('Remove special character from pan Id')
            return 1
        }
        if(/[0-9]/.test(str.substring(0,5)) || /[A-Z]/.test(str.substring(5,9)) || /[0-9]/.test(str.charAt(9))){
            setError('Incorrect pan number please enter a valid one')
            return 1
        }

    }

    function verifyAadhar(str){
        setError('')
        if(str.length!==12){
            setError('Aadhar Id is need to be of 12 digits')
            return 1
        }
            
        if(/[A-Z]/.test(str) || /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/.test(str)){
            setError('Incorrect Aadhar Id')
            return 1
        } 

        return 0
    }

    function verifyDL(str){
        setError('')
        if(str.length!==15){
            setError('Licence Id is need to be of 15 characters')
            return 1
        }

        if(/[0-9]/.test(str.substring(0,2)) || /[A-Z]/.test(str.substring(2,4)) 
            || /[A-Z]/.test(str.substring(4,14)) 
        ){
            setError('Incorrect Driving Licence')
            return 1
        }
        return 0 
    }

    function onNext(){
        if(mobile==''||govIdType==''||govId==''||address==''){
            setError('All fields must be required')
            return
        }
        
        if(verifyMobileNo())
        return 

        
        if(govIdType=='Aadhar' && verifyAadhar(govId)  ) 
            return
        if( govIdType=='PAN' && VerifyPanNo(govId) )
            return  
        if( govIdType=='Licence' && verifyDL(govId)) 
            return  


        setStep(4)
    }
    function onPrev(){
        setStep(2)
    }
    return (
        <div className="text-center">
            <h5 id="simple-modal-title" >
                <b>Owner Details</b>
            </h5>
            
            
        <div className="text-danger">{error?`* ${error}`:''}</div>
        <br/>
        <div className="mx-auto" style={{width:"60%"}}>
            <div className="mb-3 d-flex justify-content-between">
                <label 
               
                htmlFor="ownerMobile" className="htmlForm-label">Mobile</label>
                <input 
                value={mobile}
                onChange={(e)=>{setValues({...values,mobile:e.target.value.trim().toUpperCase()})}}
                style={{width:"200px",height:"30px"}}
                required type="tel" name="ownerMobile" className="htmlForm-control" />
            </div>
            <div className="htmlForm-floating d-flex justify-content-between">
                <label 
                
                htmlFor="govIdType">
                    Gov ID Type
                </label>
                <select 
                value={govIdType}
                onChange={(e)=>{setValues({...values,govIdType:e.target.value})}}
                style={{width:"200px",height:"30px",marginBottom:"10px"}}
                name="govIdType" 
                className="htmlForm-select"
                aria-label="Floating label select example">
                    <option value="">None</option>
                    <option value="Aadhar">Aadhar</option>
                    <option value="PAN">PAN</option>
                    {/* <option value="VoterId">Voter ID</option> */}
                    <option value="Licence" >Licence ID</option>
                </select>
                
            </div>
            <div className="mb-3 d-flex justify-content-between">
                <label htmlFor="govId" className="htmlForm-label">Gov ID</label>
                <input 
                value={govId} 
                onChange={(e)=>{setValues({...values,govId:e.target.value.trim().toUpperCase()})}} 
                style={{width:"200px",height:"30px"}} 
                required type="text" name="govId" className="htmlForm-control" />
            </div> 
            
            <div className="mb-3 d-flex justify-content-between">
                <label htmlFor="ownerAddress" className="htmlForm-label">Address</label>
                <input 
                value={address}
                onChange={(e)=>{setValues({...values,address:e.target.value})}}
                style={{width:"200px",height:"30px"}}
                required type="text"  name="ownerAddress" className="htmlForm-control" />
                
            </div>
            <div className="text-center">
            
        </div>
        </div>
            <div>
                <Button variant="primary" onClick={onPrev} style={{marginRight:"15px"}}>
                    <ArrowBackIcon/>
                    <span style={{marginLeft:"15px"}}>Prev</span>
                    
                </Button>
                <Button variant="primary" onClick={onNext} style={{marginLeft:"15px"}}>
                    
                    <span style={{marginRight:"15px"}}>Next</span>
                    <ArrowForwardIcon/>
                </Button>
            </div>
        </div>
    )
}
