import React,{useState,useEffect} from 'react'
import {useHistory} from 'react-router-dom'

import {useData,ACTIONS} from '../contexts/DataContext'


export default function PropertyByCities() {
    const {dispatch} = useData()
    const history =  useHistory()

    const [city,setCity] = useState('')

    

    useEffect(()=>{
        
        function handleClick(){
            if(city !== '') {
             dispatch({type:ACTIONS.FILTER_BY_CITY,payload:{city}})
             history.push('/')
            }
         }
         handleClick()
    },[city])

    return (
    <div>
        <div className="mx-auto" style={{width:'80vw'}}>
        <h3>Explore Properties from Various Cities</h3>
        </div>
        
     <div className="d-flex justify-content-evenly mx-auto" style={{flexWrap:"wrap",width:"90vw",marginTop:"20px"}}>    
       <div className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}}>
            <img src="https://www.holidify.com/images/cmsuploads/compressed/5621259188_e74d63cb05_b_20180302140149.jpg" 
            onClick={()=>{setCity('New Delhi')}} style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('New Delhi')}} className="d-flex flex-column justify-content-center">
                <b style={{margin:"15px 0 0 0"}}>Delhi /NCR</b>
                <p>20+ Properties</p>
            </div>
        </div>
        <div  value='Mumbai' className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}} >
            <img onClick={()=>{setCity('Mumbai')}} src="https://images.indianexpress.com/2020/03/mumbai-759.jpg" style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('Mumbai')}} className="d-flex flex-column justify-content-center">
                <b style={{margin:"15px 0 0 0"}}>Mumbai</b>
                <p>10+ Properties</p>
            </div>
        </div>
        <div  className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}} >
            <img onClick={()=>{setCity('Kolkata')}} 
            src="https://cdn.britannica.com/91/110191-050-7BCFD56B/Victoria-Memorial-Hall-Kolkata-India.jpg" 
            style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('Kolkata')}} className="d-flex flex-column justify-content-center">
                <b style={{margin:"15px 0 0 0"}}>Kolkata</b>
                <p>10+ Properties</p>
            </div>
        </div>
        <div  className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}} >
            <img onClick={()=>{setCity('Chennai')}} 
            src="https://www.fabhotels.com/blog/wp-content/uploads/2019/02/Spend-a-day-in-Mylapore-Unearthing-Legends-and-Folklore.jpg"
            style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('Chennai')}} className="d-flex flex-column justify-content-center">
                <b style={{margin:"15px 0 0 0"}}>Chennai</b>
                <p>13+ Properties</p>
            </div>
        </div>
        <div  className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}} >
            <img onClick={()=>{setCity('Bangalore')}} 
            src="https://i.pinimg.com/originals/c5/6a/47/c56a47e985fd065d724b0bb375d75eea.jpg"
            style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('Bangalore')}} className="d-flex flex-column justify-content-center">
                <b  style={{margin:"15px 0 0 0"}}>Bangalore</b>
                <p >15+ Properties</p>
            </div>
        </div>
        <div  className="d-flex justify-content-center" style={{width:"30vw",cursor:"pointer",margin:"10px 0"}} >
            <img onClick={()=>{setCity('Hyderabad')}} 
            src="https://www.hlimg.com/images/stories/738X538/mattress-in-hyderabad_1547551233-175e.jpg" 
            style={{height:"90px",width:"125px",marginRight:"10px"}}/>
            <div onClick={()=>{setCity('Hyderabad')}} className="d-flex flex-column justify-content-center">
                <b style={{margin:"15px 0 0 0"}}>Hyderabad</b>
                <p>10+ Properties</p>
            </div>
        </div>
</div>
</div> 
    )
}
