import React,{useState,useEffect} from 'react'

import FormB from './multi-form/FormB'
import FormA from './multi-form/FormA'
import FormC from './multi-form/FormC'
import FormD from './multi-form/FormD'
import FormE from './multi-form/FormE'
import FormF from './multi-form/FormF'
import FinalForm from './multi-form/FinalForm'
import axios from 'axios'
import {base_url} from '../contexts/DataContext'

export default function PostPropertyForm() {

        let initialValues = {

        ptyType:'',
        purpose:'',
        rentF:'',
        price:'',
        rooms:'',
        floors:'',
        bath:'',
        area:'',
        city:'',
        lat:'',
        lon:'',
        landmarks:'',
        title:'',
        desc:'',
        mobile:'',
        govId:'',
        govIdType:'',
        address:''
    }
    const [values,setValues] = useState(initialValues)
    let [step,setStep] = useState(1)
 
    async function submitForm(){
        
            let response = await axios.post(`${base_url}/add-new-property`,values)
            console.log(response)
        
    }
    switch(step){
        case 1:
            
            return (
                <FormA step={step} setStep={setStep} values={values} setValues={setValues} />
            )
        case 2:
            
            return (
                <FormB step={step} setStep={setStep} values={values} setValues={setValues}/>
            )
        case 3:
            
            return(
                <FormC step={step} setStep={setStep} values={values} setValues={setValues}/>
            )
        case 4:
            return (
                <FormD step={step} setStep={setStep} values={values} setValues={setValues}/>
            )
        case 5 :
            return (
                <FormE step={step} setStep={setStep} values={values} setValues={setValues}/>
            )
        case 6 :
            return (
                <FormF step={step} setStep={setStep} values={values} setValues={setValues} submitForm={submitForm}/>
            )
        default:
            return(
                <div>Default</div>
            )
    }

                
        }




        