import React,{useContext,useState,useEffect} from 'react'
import {createUserWithEmailAndPassword,
    onAuthStateChanged,
    signInWithEmailAndPassword,signOut,
    sendPasswordResetEmail,updateEmail,updatePassword } from 'firebase/auth'

import {Auth} from './firebase-config'

const AuthContext= React.createContext()


export function useAuth(){
    return useContext(AuthContext)
}

export function AuthProvider({children}){

    const [currentUser,setCurrentUser] = useState()
    const [loading,setLoading] = useState(true)

    function signup(email,password){
        return createUserWithEmailAndPassword(Auth,email,password)
    }
    function signin(email,password){
        return signInWithEmailAndPassword(Auth,email,password)
    }
    function signout(){
        return signOut(Auth)
    }
    function resetpassword(email){
        return sendPasswordResetEmail(Auth,email)
    }
    function updateemail(email){
        // console.log('i m update email from auth context',currentUser)
        return updateEmail(currentUser,email)
    }
    function updatepassword(pass){
        //console.log('i m update password from auth context',currentUser)
        return updatePassword(currentUser,pass)
    }
    useEffect(()=>{
        const unSubscribe = onAuthStateChanged(Auth, user => {
            setCurrentUser(user)
            setLoading(false)
          });
          return unSubscribe
    },[])
    const value ={
        currentUser,
        signup,
        signin,
        signout,
        resetpassword,
        updateemail,
        updatepassword,
    }
    return (
        <AuthContext.Provider value={value}>
            {!loading && children}
        </AuthContext.Provider>
    )
}