import React from 'react'
import PeopleIcon from '@material-ui/icons/People';
import PaymentIcon from '@material-ui/icons/Payment';
import DirectionsIcon from '@material-ui/icons/Directions';
import DateRangeIcon from '@material-ui/icons/DateRange';
import CropFreeIcon from '@material-ui/icons/CropFree';
import CropSquareIcon from '@material-ui/icons/CropSquare';
import ExploreIcon from '@material-ui/icons/Explore';
import SingleBedIcon from '@material-ui/icons/SingleBed';
import NaturePeopleIcon from '@material-ui/icons/NaturePeople';
import StairsIcon from '../images/stairs.png'
import land from '../images/land.png'
import Residential from '../images/residential.png'
import SecurityIcon from '@material-ui/icons/Security';
import {Button} from 'react-bootstrap'
import {base_url} from '../contexts/DataContext'

export default function PropertyDetail({data}) {

    const {_id,description,area,price,rooms,bath,title} = data

    function getRandomDir(){
        
        const arr = ['east','west','north','south']
        const random = Math.floor(Math.random() * arr.length)
        return arr[random]
    }
    function getFurnished(){
        
        const arr = ['Furnished','Non Furnshied','Semi Furnished']
        const random = Math.floor(Math.random() * arr.length)
        return arr[random]
    }
    function getFloor(){
        
        const arr = [1,2,3,4,5]
        const random = Math.floor(Math.random() * arr.length)
        return arr[random]
    }
    function getAge(){
        
        const arr = [1,2,3,4,5,6,7,8,9]
        const random = Math.floor(Math.random() * arr.length)
        return arr[random]
    }
    async function handleClick(){
        console.log(_id)
        await fetch(`${base_url}/create-checkout`,{
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body: JSON.stringify({
                id:_id,
                items:[
                    {id:1,quantity:1},
                ]
            })
        }).then(res=>{
                if(res.ok) return res.json({hi:'howdy'})
                return res.json().then(json => Promise.reject(json))
            }).then(({url})=>{
                window.location = url
                //console.log('hello')
            }).catch(e =>{
                console.log(e)
            })
        
    }
    return (
        <div className="text-dark mx-auto" style={{width:"50vw"}}>
            <h4 className="text-dark text-center" style={{margin:"15px 0"}}>
                {title}
            </h4>
            <div className="text-dark" style={{margin:"25px 0",fontSize:"18px"}}>
                {description}
            </div>
            <div className="bg-white d-flex justify-content-evenly" style={{margin:"10px 0",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}>
                <div style={{width:"50%"}}>
                    <div style={{color:"gray"}}><PaymentIcon style={{color:"green"}}/> Pricing</div>
                    <div style={{marginLeft:"30px"}}>{price}</div>
                </div>
                
                <div style={{width:"50%"}}>
                    <div style={{color:"gray"}}><img src={Residential} style={{height:"20px",width:"20px"}}/> Configuration</div>
                    <div style={{marginLeft:"30px"}}>{rooms} bedrooms, {bath} bathrooms</div>
                </div>
            </div>
            <div className="bg-white d-flex justify-content-evenly" style={{margin:"10px 0",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}>
                <div style={{width:"50%"}}>
                    <div style={{color:"gray"}}><SecurityIcon style={{color:"green"}}/> Security</div>
                    <div style={{marginLeft:"30px"}}>yes</div>
                </div>
                <div style={{width:"50%"}}>
                    <div><CropSquareIcon style={{color:"gray"}}/> Area</div>
                    <div style={{marginLeft:"30px"}}> {area} sqft</div>
                </div>
                
               
            </div>
            <div className="d-flex justify-content-between" style={{margin:"10px 0"}}>
                <div class="floors" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}> 
                    <div><img src={StairsIcon} style={{color:'gray',height:"20px",width:"20px"}}/> Floors in property</div>
                    <div style={{marginLeft:"30px"}}>{getFloor()} floors</div>
                </div>
                <div className="facing" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}>
                    <div style={{color:"gray"}}><ExploreIcon style={{color:"black"}}/> Facing</div>
                    <div style={{marginLeft:"30px"}}>{getRandomDir()}</div>
                    
                </div>
            </div>
            <div className="d-flex justify-content-between" style={{margin:"10px 0"}}>
                <div class="overlooking" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}> 
                    <div style={{color:"gray"}}><NaturePeopleIcon style={{color:"green"}}/> Overlooking</div>
                    <div style={{marginLeft:"30px"}}>Park/Garden,Others</div>
                </div>
                <div class="propertyAge" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(0,0,0,0.31)'}}>
                    <div style={{color:"gray"}}><DateRangeIcon style={{color:"green"}}/> Property Age</div>
                    <div style={{marginLeft:"30px"}}>{getAge()} year old</div>
                    
                </div>
            </div>
            <div className="d-flex justify-content-between" style={{margin:"10px 0"}}>
                <div class="" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(234,173,78,0.31)'}}> 
                    <div style={{color:"gray"}}><SingleBedIcon style={{color:"brown"}}/> furnishing</div>
                    <div style={{marginLeft:"30px"}}>{getFurnished()}</div>
                </div>
                <div class="propertyAge" style={{width:"45%",boxShadow:'0 10px 10px -10px rgba(18,88,255,0.31)'}}>
                    <div style={{color:"gray"}}><PeopleIcon style={{color:"lightblue"}}/> Available for</div>
                    <div style={{marginLeft:"30px"}}>Every One</div>
                    
                </div>
            </div>
            <div className="d-flex justify-content-evenly" style={{margin:"20px 0",height:"50px",boxShadow: '-5px 4px 10px -2px rgba(0,0,0,0.31)'}}>
                
                <div className="" style={{fontSize:"20px"}}>pay 15% now and get immediate booking </div>
                {/* <button  onMouseOver={(e)=>{console.log(e)}} ></button> */}
                <Button onClick={handleClick} variant="outline-success" style={{width:"25%",borderRadius:"25px"}}>Buy this Property</Button>
            </div>
        </div>
    )
}
